.ic_small_header_contact_link,
.ic_small_header_contact_text,
.ic_small_header_social_link {
  line-height: inherit;
  display: inline-block;
}

/* <span> surrounding the contact stuff */
#ic_s_header_contact {
  position: absolute;
  left: 2.5em;
}

/* <span> after the icon surrunding the link text */
.ic_small_header_contact_text {
  padding-left: 0.2em;
  margin-right: 1em;
}

/* Social link icons */
#ic_small_header_social {
  position: absolute;
  right: 2.5em;
}

.ic_small_header_social_link:not(:last-child) {
  margin-right: 1em;
}

/* social link icons */
.ic_small_header_social_link i {
  line-height: inherit;
  font-size: 1.4em;
}

/*
 * ### 3.2 Logo & Menu Header
 */
#ict_header {
  position: fixed;
  top: 28px;
  width: 100%;
  background-color: #13004d;
  /* box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12); */
  z-index: 9999;
  border-bottom: 5px solid rgba(0, 255, 0, 0.8);
  height: 58px;
}

/* reset bootstrap first */
.navbar-brand {
  padding-top: 0;
  padding-bottom: 0;
}

.navbar-default .navbar-nav > li:last-child a {
  padding-right: 43px;
}

.navbar-default .navbar-nav > li > a {
  color: WhiteSmoke;
  height: 58px;
}
.navbar-default .navbar-nav > li > a:hover {
  background-color: red;
}
@media screen and (min-width: 768px) {
  .navbar-default .navbar-nav > li > a {
    padding-left: 2px;
    padding-right: 2px;
  }
}
@media screen and (min-width: 992px) {
  .navbar-default .navbar-nav > li > a {
    font-size: 1em;
    padding-left: 10px;
    padding-right: 10px;
  }
}
@media screen and (min-width: 1200px) {
  .navbar-default .navbar-nav > li > a {
    font-size: 1.3em;
    height: 58px;
  }
}

#ic_logo {
  position: absolute;
}

/* wp theme_mod( 'custom-logo ') style */
.size-custom_logo {
  height: 58px;
  margin-left: 2em;
}

#ic_logo h1, #ic_logo p {
  display: inline;
  padding-left: 1em;
}

#ic_logo h1 {
  line-height: 1.8em;
}

/* remove boder from line height */
#ic_logo p {
  line-height: 3.6em;
}

/* Top Menu */
.ict_header .menu-item a {
  text-decoration: none;
  padding: 0 1em 0 1em;
  font-weight: bold;
  border-bottom: 0.4em solid rgba(0, 255, 0, 0.4);
  line-height: 3.6em;
  /* remove border from line height */
  height: 4em;
}

.ict_header .menu-item a:hover {
  /*	background-color: rgb(19,0,77); */
  /*	background-color: rgba(255,255,255,0.2); */
  /*	border-bottom: 0.4em solid rgb(54,0,217); */
  background-color: rgba(255, 255, 255, 0.2);
  z-index: 9999;
}

.ict_header .current-menu-item {
  background-color: rgba(255, 255, 255, 0.2);
}

/* general post & page style
 * body hase .page class too, so the margrin & color will be wrong for pages 
 */
.post,
div.page {
  margin: 0 2em;
  margin-bottom: 2em !important;
  box-shadow: 2px 2px 6px 0 rgba(0, 0, 0, 0.4), 0 2px 10px 0 rgba(0, 0, 0, 0.4);
  background-color: gainsboro;
  border-top: 0.4em solid lime;
}

.post-title,
.page-title {
  width: 100%;
  background-color: #13004d;
  color: WhiteSmoke;
  padding: 0.3em 1.2em;
  font-size: 1.6em;
}

.post p,
div.page p {
  padding: 2em;
  color: #13004d;
}

h1.post-title {
  margin: 0;
}

/* Frontpage portfolio */
div.ic_portfolio_row {
  margin-top: 2em;
  margin-left: 4em;
  margin-right: 4em;
}

div.ic_portfolio_row .post {
  margin: auto;
}

div.ic_portfolio_row .post:hover {
  background-color: #f0f0f0;
}

/* TODO: Put responsive code together */
@media screen and (min-width: 600px) {
  div.ic_portfolio_row .post {
    float: left;
    width: 33.3333333%;
  }

  div.ic_portfolio_row .post:first-child {
    margin-left: -2em;
  }

  div.ic_portfolio_row .post:nth-child(2) {
    margin-left: 2em;
    margin-right: 2em;
  }

  div.ic_portfolio_row .post:last-child {
    margin-right: -2em;
  }
}
@media screen and (max-width: 600px) {
  div.ic_portfolio_col:not(:last-child) {
    margin-bottom: 2em;
  }
}
/* footer stuff */
div#footer {
  width: 100%;
  background-color: #13004d;
  border-top: 0.4em solid lime;
}

div#footer ul.menu {
  padding-left: 1em;
}

div#footer li.menu-item {
  list-style: none;
}

div#footer li.menu-item a {
  display: inline;
  background: none;
}

.widget-container {
  padding: 2em;
}

/* TODO: Put responsive code together */
@media all and (min-width: 600px) {
  div#footer div.widget-container {
    float: left;
    width: 33.3333333%;
  }
}
/* Unknow required (by theme checker) elements */
/* 4.1 Gallery settings
 * REMOVE CLASSE FROM t
 */
.gallery-size-full,
.gallery-size-large {
  overflow: hidden;
  margin: 0 2em !important;
  /* overwrite inline style, why? */
}

